@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.results{display:flex}.results.loading{height:96vh;display:flex;align-items:center;justify-content:center}.results .office-result{width:33%;margin:0 1em;text-align:center}.results .current-location{opacity:.3}.results .day{border-right:1px solid #ccc;width:20%;padding:0 1em}.results .day:last-child{border-right:0}.results .day .date{font-size:.8em;color:#999}.results .flights .flight-info{border-bottom:1px dotted #ccc}.results .flights .flight-info:first-child{font-weight:bold}.results .flights .flight-info:last-child{border-bottom:0}.results .flights .flight-info em{color:#989898;font-size:.8em;display:block}

.preferences{margin:1em;display:flex;justify-content:center}.preferences input[type="number"]{width:2em;margin:0 1em}

